<template>  
  <div>
    <img :src="productObj.image" class="roomg-img" style="width: 30%;" />
    <h4 @click="sendOpenMsg()">{{productObj.title}}</h4>
    <b>{{productObj.content}}</b><a>   {{productObj.price}}원</a>
  </div>
</template>

<script>
export default {
    name : "ProductList",
    methods: { 
        sendOpenMsg() {
            this.$emit('openModal');
        }
    },
    props : {
        isModalOpen : Boolean,
        productObj : Object
    }
}
</script>

