<template>
  <div class="black-bg" v-if="isModalOpen == true">
    <div class="white-bg">
        <img :src="maindatas[idx].image" style="width: 30%;"/>
        <h4>{{ maindatas[idx].title }}</h4>
        <b>{{ maindatas[idx].content }}</b><br>
        <select v-model="month">
          <option v-for="i in 12" :key="i">{{ i }}</option>
        </select>
        <a>months select : {{ maindatas[idx].price * month}}</a>원<br>   
        <button @click="sendCloseMsg()">close</button><br><br>

        <input v-model="month" />
        <input type="range" min="1" max="12" v-model="month"/>
    </div>
  </div>
</template>

<script>
export default {
    name : "ProductModal",
    data() {
      return {
        month : 1
      }
    },
    watch : {
      month(data, org) {
        if(data > 12) {
          alert('Dont over then 12')
          this.month = org
        }
        if(isNaN(data)){
          alert('This field only number')
          this.month = 1
        }
      }
    },
    props : {
      maindatas : Array,
      isModalOpen : Boolean,
      idx : Number
    },
    methods : { 
      sendCloseMsg() {
        this.$emit('closeModal')
      }
    },
    updated() {
      if(this.month == 2){
        alert("input 2")
        this.month = 4
      }
    }
}
</script>

<style>
.black-bg {
  width: 100%; height: 100%;
  background: rgba(0,0,0,0.5);
  position: fixed; padding: 20px;
}

.white-bg {
  width: 100%; background: white;
  border-radius: 8px;
  padding: 20px;
}
</style>