<template>

  <transition name="fade">
    <ProductModal @closeModal="closeModal()" 
                :maindatas="maindatas" 
                :isModalOpen="isModalOpen" 
                :idx="idx"/>
  </transition>
  
  <div class="menu">
    <a v-for="value in titles" :key="value">{{ value }}</a>
  </div> 

  <DiscountContents v-if="isShowDiscountContents" :discountPer="discountPer"/>

  <button @click="priceSort()">sorting price</button>
  <button @click="sortBack()">sort back</button>

  <ProductList @openModal="openModal(i)" 
               :productObj="maindatas[i]" 
               v-for="(value, i) in maindatas" :key="i" />

</template>

<script>

import data from "./data.js";
import DiscountContents from "./components/product/DiscountContents.vue";
import ProductModal from "./components/product/ProductModal.vue";
import ProductList from "./components/product/ProductList.vue";

export default {
  name: 'App',
  data(){
    return {
      titles: ['Home','Product','About','BowWow'],
      isModalOpen: false,
      maindatas: data, 
      maindatasOrg: [...data],
      idx: 0,
      isShowDiscountContents: true,
      discountPer: 20
    }
  },
  methods: {
    openModal(id) {
      this.isModalOpen = true
      this.idx = id
    },
    closeModal() {
      this.isModalOpen = false
    },
    priceSort() {
      this.maindatas.sort(function(a,b){
        return a.price - b.price
      })
    },
    sortBack() {
      this.maindatas = [...this.maindatasOrg]
    }
  },
  components: {
    DiscountContents, ProductModal, ProductList
  },
  mounted() {
    setInterval(()=>{
      if(this.discountPer > 0){
        this.discountPer--
      } 
    },1000)
  }
}

</script>

