body {
  margin: 0
}

div {
  box-sizing: border-box;
} 
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

.menu {
  background: darkslateblue;
  padding: 15px;
  border-radius: 5px;
}

.menu a {
  color: white;
  padding: 10px;
}

.babyImg {
  width: 10%;
  height: 10%;
}

.room-img {
  width: 60%;
  height: 60%;
}

.fade-enter-from {
  transform: translateY(-1000px);
}

.fade-enter-active {
  transition: all 1s;
}

.fade-enter-to {
  transform: translateY(0px);
}

.fade-leave-from {
  transform: translateY(0px);
}

.fade-leave-active {
  transition: all 1s;
}

.fade-leave-to {
  transform: translateY(-1000px);
}
</style>
